.page {
  margin: 0;
  height: 100%;
  font-size: 16px;
  line-height: 24px;
  color: #a07b50;
  font-family: "Roboto", "Arial", sans-serif;
  background-color: #181617;
}

.page__body {
  margin: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.page__main {
  flex: 1 1 auto;
}

.page__footer {
  background-color: #151313;
}

.header {
  min-height: 684px;
  max-width: 1366px;
  width: 100%;
  margin: 0 auto;
  background-image: url("/img/header-background-desktop@1x.webp");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: right;
}
@media (min-resolution: 2dppx) {
  .header {
    background-image: url("/img/header-background-desktop@2x.webp");
  }
}
@media (max-width: 768px) {
  .header {
    background-image: url("/img/header-background-tablet@1x.webp");
    min-height: 595px;
    background-position-x: right;
    background-position-y: bottom;
    background-size: 534px auto;
  }
}
@media (max-width: 768px) and (min-resolution: 2dppx) {
  .header {
    background-image: url("/img/header-background-tablet@2x.webp");
  }
}
@media (max-width: 320px) {
  .header {
    background-image: url("/img/header-background-mobile@1x.webp");
    min-height: 617px;
    background-position: bottom;
    background-size: 320px auto;
  }
}
@media (max-width: 320px) and (min-resolution: 2dppx) {
  .header {
    background-image: url("/img/header-background-mobile@2x.webp");
  }
}

