  .pets__slider {
    margin-bottom: 33px;
  }
}

.pets-slider__block {
  display: grid;
  grid-template-columns: 0.52fr 0.48fr;
  grid-template-rows: 220px 220px;
  gap: 30px;
}
@media (max-width: 768px) {
  .pets-slider__block {
    display: flex;
    gap: 0;
