  .pets-slider__slide {
    width: 100%;
    margin: auto 0;
  }
}

.pets-slider__slide--hidden {
  display: none;
}

.pets-slider__photo-img {
  display: block;
  object-fit: cover;
  object-position: center;
}
@media (max-width: 768px) {
  .pets-slider__photo-img {
    margin: auto;
    width: 80%;
    height: 100%;
  }
}

.pets-slider-button {
  padding: 0;
  border-width: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  font-size: 0;
  position: absolute;
}

.pets-slider-button:hover {
  color: #ffffff;
}

.pets-slider-button--prev {
  display: none;
}
@media (max-width: 768px) {
  .pets-slider-button--prev {
    display: block;
    top: 50%;
  }
}

.pets-slider-button--next {
  display: none;
}
@media (max-width: 768px) {
  .pets-slider-button--next {
    display: block;
    top: 48%;
    right: 0;
    transform: rotate(180deg);
  }
}

